<template>
  <div class="page-content" v-loading="loading">
    <div class="c-650">
      <div class="sub-header">
        <div class="sub-header-title"></div>
      </div>
      <el-form
        label-position="left"
        label-width="80px"
        :model="manager"
        ref="manager"
      >
        <el-form-item label="" prop="name" :rules="rules.name">
          <el-input v-model="manager.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone" :rules="rules.phone">
          <el-input v-model.number="manager.phone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="role_id" :rules="rules.role">
          <el-select v-model="manager.role_id" placeholder="">
            <el-option
              v-for="item in roles"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleManagerCreate"
            ></el-button
          >
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import serviceManager from "@/globals/service/manager.js";
import serviceRole from "@/globals/service/role.js";

export default {
  data() {
    return {
      loading: true,
      roles: [],
      manager: {
        name: "",
        phone: "",
        role_id: ""
      },
      rules: {
        name: { required: true, message: "", trigger: "blur" },
        role: { required: true, message: "", trigger: "change" },
        phone: [
          { required: true, message: "", trigger: "change" },
          {
            pattern: /^1[3456789]\d{9}$/,
            message: "",
            trigger: "blur"
          }
        ]
      }
    };
  },
  created() {
    Promise.all([serviceRole.index()])
      .then(([roles]) => {
        this.roles = roles;
      })
      .finally(() => (this.loading = false));
  },
  methods: {
    handleManagerCreate() {
      this.$refs["manager"].validate(valid => {
        if (valid) {
          this.loading = true;
          this.manager.phone = String(this.manager.phone);
          serviceManager
            .create(this.manager)
            .then(() => {
              this.$router.push({ name: "SettingManager" });
              this.$message.success("");
            })
            .finally(() => (this.loading = false));
        }
      });
    }
  }
};
</script>

<style lang="less" scoped></style>
